label.thumb { position: relative; }

label.thumb > input[type="radio"] { visibility: hidden; position: absolute; }

label.thumb > input[type="radio"] + img { cursor: pointer; border: 3px solid transparent; padding: 50px; background-color: rgba(255, 255, 255, 0.4); border-radius: 5px; }

label.thumb > input[type="radio"] + img:hover { border-color: white; }

label.thumb > input[type="radio"]:checked + img { border-color: #B6F890; }

label.thumb span { position: absolute; bottom: 25px; left: 0; right: 0; font-family: 'Roboto-Medium', sans-serif; font-size: 16px; color: #0F110F; line-height: 24px; pointer-events: none; }

.radio-buttons { background-color: rgba(255, 255, 255, 0.4); border-radius: 5px; padding: 5% 10%; /* Create the indicator (the dot/circle - hidden when not checked) */ /* Show the indicator (dot/circle) when checked */ /* Style the indicator (dot/circle) */ }

.radio-buttons label { display: block; position: relative; padding-left: 35px; margin-bottom: 12px; cursor: pointer; font-size: 15px; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; text-align: left; }

.radio-buttons label input { position: absolute; opacity: 0; cursor: pointer; }

.radio-buttons .checkmark { position: absolute; top: 0; left: 0; height: 17px; width: 17px; border-radius: 50%; border: 3px solid #000000; background: transparent; }

.radio-buttons label:hover input ~ .checkmark { background-color: #B6F890; }

.radio-buttons label input:checked ~ .checkmark { background-color: #B6F890; }

.radio-buttons .checkmark:after { content: ""; position: absolute; display: none; }

.radio-buttons label input:checked ~ .checkmark:after { display: block; }

.radio-buttons label .checkmark:after { top: 6px; left: 6px; width: 6px; height: 6px; border-radius: 50%; background: #000000; }

.preview-slider { background-color: rgba(255, 255, 255, 0.4); border-radius: 5px; padding: 5% 5%; }

.preview-slider .slider { margin: 3% 0 0 0; padding: 0; -webkit-appearance: none; width: 100%; height: 15px; border-radius: 5px; background: #d3d3d3; }

.preview-slider .slider::-webkit-slider-thumb { -webkit-appearance: none; appearance: none; width: 25px; height: 25px; border-radius: 50%; background: #B6F890; cursor: pointer; }

.preview-slider .slider::-moz-range-thumb { width: 25px; height: 25px; border-radius: 50%; background: #B6F890; cursor: pointer; }

.img-carousel { width: 100%; }

.img-carousel img { width: 28%; cursor: pointer; -webkit-transition: width 0.2s; /* Safari */ transition: width 0.2s; }

.img-carousel img.selected { width: 36%; border: 3px solid #B6F890; }

/** SIMPLE GRID (C) ZACH COLE 2016 */
/* UNIVERSAL */
html, body { height: 100%; width: 100%; margin: 0; padding: 0; left: 0; top: 0; font-size: 100%; }

.font-light { font-weight: 300; }

.font-regular { font-weight: 400; }

.font-heavy { font-weight: 700; }

/* POSITIONING */
.left { text-align: left; }

.right { text-align: right; }

.center { text-align: center; margin-left: auto; margin-right: auto; }

.justify { text-align: justify; }

/* ==== GRID SYSTEM ==== */
.container { width: 90%; margin-left: auto; margin-right: auto; }

.row { position: relative; width: 100%; }

.row [class^="col"] { float: left; margin: 0.5rem 2%; min-height: 0.125rem; }

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 { width: 96%; }

.col-1-sm { width: 4.33%; }

.col-2-sm { width: 12.66%; }

.col-3-sm { width: 21%; }

.col-4-sm { width: 29.33%; }

.col-5-sm { width: 37.66%; }

.col-6-sm { width: 46%; }

.col-7-sm { width: 54.33%; }

.col-8-sm { width: 62.66%; }

.col-9-sm { width: 71%; }

.col-10-sm { width: 79.33%; }

.col-11-sm { width: 87.66%; }

.col-12-sm { width: 96%; }

.row::after { content: ""; display: table; clear: both; }

.hidden-sm { display: none; }

@media only screen and (min-width: 33.75em) { /* 540px */
  .container { width: 80%; } }

@media only screen and (min-width: 45em) { /* 720px */
  .col-1 { width: 4.33%; }
  .col-2 { width: 12.66%; }
  .col-3 { width: 21%; }
  .col-4 { width: 29.33%; }
  .col-5 { width: 37.66%; }
  .col-6 { width: 46%; }
  .col-7 { width: 54.33%; }
  .col-8 { width: 62.66%; }
  .col-9 { width: 71%; }
  .col-10 { width: 79.33%; }
  .col-11 { width: 87.66%; }
  .col-12 { width: 96%; }
  .hidden-sm { display: block; } }

@media only screen and (min-width: 60em) { /* 960px */
  .container { width: 75%; max-width: 60rem; } }

nav { z-index: 10; width: calc(100% - 80px); height: 40px; position: absolute; top: 0; padding-top: 5px; margin: 0px 40px; opacity: 1; }

nav #nav-links { float: right; margin: 0; padding: 0; }

nav #nav-links li { display: inline; padding: 0; margin: 0; }

nav #nav-links a:link, nav #nav-links a:visited { color: white; padding: 10px 30px; text-decoration: none; }

nav #logo { position: absolute; left: 0; right: 0; top: -60px; margin-left: auto; margin-right: auto; width: 120px; height: 120px; background: black; background: rgba(0, 0, 0, 0.6); border-radius: 50%; }

nav #logo .logo-container { position: relative; width: 100%; height: 100%; }

