  nav #mobile-navigation .nav-toggle i { font-size: 40px; }
  nav #mobile-navigation #mobile-links { float: right; margin: 0; padding: 0; height: 100%; width: 0; position: fixed; z-index: 1; top: 0px; left: 0; background-color: #B1A664; overflow-x: hidden; transition: 0.5s; padding-top: 30px; }
  nav #mobile-navigation #mobile-links a { color: white; font-size: 30px; text-decoration: none; }
  nav #mobile-navigation #mobile-links .nav-toggle { position: absolute; right: 0; top: 0; }
  nav #mobile-navigation #mobile-links.open { width: 300px !important; } }

